import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import styles from './index.css';
import MainSwiper from './MainSwiper';
import errorBoundary from "@src/components/errorBoundary"
import { formatImage } from '@ifeng/public_method';

const Header = ({ content, statusBarHeight, rnum }) => {
    const [activeIndex, setActiveIndex] = useState(0);
    const [imageList, setImageList] = useState([]);

    useEffect(
        () => {
            const list = [];

            content.forEach((c, index) => {
                list.push(
                    <div
                        key={index}
                        className={styles.mask}
                        style={{
                            zIndex: activeIndex === index ? 1 : 0,
                            backgroundImage: `url("${formatImage(c.posterUrl, 3, 2, 0.1)}")`,
                        }}
                    />,
                );
            });
            setImageList(list);
        },
        [activeIndex],
    );

    const handleChangeIndex = index => setActiveIndex(index);

    return (
        <div className={styles.header}>
            <div className={styles.maskWp} key={activeIndex} style={{ paddingTop: `${statusBarHeight}px` }}>
                <div className={styles.whiteMask}>
                    {imageList.map(item => {
                        return item;
                    })}
                </div>
            </div>
            <MainSwiper rnum={rnum} content={content} onChangeIndex={handleChangeIndex} />
        </div>
    );
};

Header.propTypes = {
    content: PropTypes.array,
    statusBarHeight: PropTypes.number,
    rnum: PropTypes.number
};

export default errorBoundary(Header);
