import React, { useState } from 'react';
import styles from './index.css';
import PropTypes from 'prop-types';
import errorBoundary from "@src/components/errorBoundary"
// import { pageStatistics } from '@ifeng/mobile_statistics';
import { publicPath, devPath } from '@src/constants';
import { formatImage } from '@ifeng/public_method';
import Observer from '@researchgate/react-intersection-observer';
// import { Grounds } from '@ifeng/client_grounds';
import IndexCollect from '@src/common/components/IndexCollect';

const Hot = props => {
    const { content, rnum } = props;
    const [startIndex, setStartIndex] = useState(0);
    const [toggleAnim, setToggleAnim] = useState(-1);

    const next = () => {
        pageStatistics({
            behavior: 'action',
            environment: 'client',
            type: 'heat_change',
        });
        if (toggleAnim === -1) {
            setToggleAnim(true);
        } else {
            setToggleAnim(!toggleAnim);
        }
        setStartIndex(startIndex + 6 > content.length ? startIndex + 6 - content.length : startIndex + 6);
    };

    const GetList = () => {
        const totalLength = content.length;

        if (startIndex + 6 <= totalLength) {
            return content.slice(startIndex, startIndex + 6);
        } else {
            const ends = content.slice(startIndex);

            return ends.concat(content.slice(0, 6 - ends.length));
        }
    };

    const jumpToDetail = (item, index) => {
        const { base62Id } = item;

        const finalUrl = process.env.NODE_ENV == 'development' ? 
        `${devPath}/details/${base62Id}?id=phmovie_${base62Id}&type=phmovie&ref=${window.isChannel ? 'fyt' : 'phmovie'}&reftype=${encodeURIComponent(
            'editor|||||||',
        )}&rnum=${rnum}_${index}&showtype=phhit&isFull=0&immersion=true&needpage=1`
        :
        `${window.location.origin}/tvTheater/mobile_details.html`


        if (window.grounds) {
            Grounds.dispatch({
                link: {
                    type: 'web',
                    url: finalUrl,
                },
                pageLogParams: {
                    ref: window.isChannel ? 'fyt' : 'phmovie',
                    rnum: `${rnum}_${index}`,
                    showtype: 'phhit',
                    type: 'phmovie',
                    reftype: 'editor|||||||',
                    id: `phmovie_${base62Id}`,
                },
            });
        } else {
            window.location.href = finalUrl;
        }
    };

    const handleObserverChange = (entry, unobserve, item, index) => {
        const { isIntersecting } = entry;

        if (isIntersecting) {
            const { base62Id } = item;

            // Grounds.expose({
            //     id: `phmovie_${base62Id}`,
            //     rnum: `${rnum}_${index}`,
            //     reftype: 'editor|||||||',
            //     ch: window.isChannel ? 'fyt' : 'phmovie',
            //     src: '',
            //     showtype: 'phhit',
            //     pagetype: 'phmovie',
            // });
            unobserve();
        }
    };

    return (
        <div className={styles.hot}>
            <div className={styles.toolbar}>
                <div className={styles.partTitle}>正在热播</div>
                {/* eslint-disable-next-line no-nested-ternary */}
                <div
                    className={`${styles.switch} ${
                        // eslint-disable-next-line no-nested-ternary
                        typeof toggleAnim === 'boolean' ? (toggleAnim ? styles.animA : styles.animB) : ''
                    }`}
                    onClick={next}>
                    <img src={require('@src/common/assets/switch.png')} alt="" />
                    换一批
                </div>
            </div>
            <div className={styles.videoList}>
                {GetList().map((a, index) => {
                    const tags = a.tags ? a.tags.split(',') : [];

                    return (
                        <Observer
                            key={Math.trunc(Math.random() * 9999)}
                            onChange={(entry, unobserve) => handleObserverChange(entry, unobserve, a, index)}>
                            <div className={styles.videoItem} onClick={() => jumpToDetail(a, index)}>
                                <IndexCollect itemData={a} />
                                <div className={styles.imagePart}>
                                    <img src={formatImage(a.miniVideoPoster, 226, 304)} alt={a.title} />
                                    <div className={styles.info}>
                                        {tags[0] ? <span className={styles.tag}>{tags[0]}</span> : <span />}
                                        {a.dataTotal && <div className={styles.nums}>全{a.dataTotal}集</div>}
                                    </div>
                                </div>
                                <div className={styles.title}>{a.title}</div>
                                <div className={styles.subTitle}>{a.summary}</div>
                            </div>
                        </Observer>
                    );
                })}
            </div>
        </div>
    );
};

Hot.propTypes = {
    content: PropTypes.array,
    rnum: PropTypes.number
};

export default errorBoundary(Hot);
