import React, { useState, useRef, useEffect } from 'react';
import PropTypes from 'prop-types';
import styles from '../index.css';
import dayjs from 'dayjs';
import Observer from '@researchgate/react-intersection-observer';
import { formatImage } from '@ifeng/public_method';

const FlowVideo = (props) => {
    const { isFirst, data, mute, onChangeMute } = props;
    const [ isEnd, setIsEnd ] = useState(false);
    const [ scrollDirection, setScrollDirection ] = useState('');
    const [ duration, setDuration ] = useState(data.__video__ ? data.__video__.duration : 0);
    const [ headerHeight, setHeaderHeight ] = useState(0);

    const videoRef = useRef();
    const timer = useRef(null);

    const scroll = () => {
        if (window.pageYOffset != null) {
            // 支持IE9 +
            return {
                left: window.pageXOffset,
                top: window.pageYOffset
            };
        } else if (document.compatMode === 'CSS1Compat') {
            return {
                left: document.documentElement.scrollLeft,
                top: document.documentElement.scrollTop
            };
        }

        return {
            left: document.body.scrollLeft,
            top: document.body.scrollTop
        };
    };

    useEffect(() => {
        setHeaderHeight(document.getElementById('toolbar').offsetHeight);
        if (isFirst) {
            // eslint-disable-next-line func-names
            window.onscroll = (function () {
                let sLeft = scroll().left;
                let sTop = scroll().top;

                // eslint-disable-next-line func-names
                return function () {
                    if (sTop !== scroll().top) {
                        if (sTop < scroll().top) {
                            // 下滚动
                            setScrollDirection('down');
                        } else {
                            // 上滚动
                            setScrollDirection('up');
                        }
                        sTop = scroll().top;
                    }
                    if (sLeft !== scroll().left) {
                        sLeft = scroll().left;
                    }
                };

            })();
        }
    }, []);

    const GetRestDuration = () => {
        return dayjs.duration(duration, 'seconds').format('mm:ss');
    };

    const handleCanPlay = () => {
        if (!timer.current) {
            timer.current = setInterval(() => {
                setDuration((data) => {
                    if (Number(data) === 0) {
                        clearInterval(timer.current);
                        timer.current = null;

                        return 0;
                    }

                    return (Number(data) - 1).toString();
                });
            }, 1000);

            videoRef.current.addEventListener('play', () => {
                if (!timer.current) {
                    timer.current = setInterval(() => {
                        setDuration((data) => {
                            if (Number(data) === 0) {
                                clearInterval(timer.current);
                                timer.current = null;

                                return 0;
                            }

                            return (Number(data) - 1).toString();
                        });
                    }, 1000);
                }
            });

            videoRef.current.addEventListener('pause', () => {
                clearInterval(timer.current);
                timer.current = null;
            });
        }

    };

    const handleMute = (e) => {
        e.stopPropagation();
        onChangeMute();
    };
    
    const handleVideoObserverChange = (entry) => {
        if (entry.intersectionRatio > 0.89) {
            pauseAll();
            videoRef.current.play();
        } else {
            videoRef.current.pause();
        }

        if (entry.intersectionRatio === 0) {
            pauseAll();
        }

        // if (entry.intersectionRatio < 0.1 && isFirst && scrollDirection === 'up') {
        //     console.log('暂停')
        //     pauseAll();
        // }
    };

    const pauseAll = () => {
        const videos = document.getElementsByClassName('__video__');

        for (let i = 0; i < videos.length; i++) {
            videos[i].pause();
        }
    };

    const getThumbnail = (data) => {
        const image = data.posterUrl || (data.thumbnails && data.thumbnails.image && data.thumbnails.image[0] && data.thumbnails.image[0].url);

        return image || '';
    };

    const getNumText = (data) => {
        if (data.extData && data.extData.dataTotal) {
            return `全${data.extData.dataTotal}集`;
        } else {
            return '';
        }
    };

    const getTags = (data) => {
        if (data.extData && data.extData.tags) {
            const arr = data.extData.tags.split(',');

            return arr[0];
        } else {
            return '';
        }
    };

    return (
        <>
            {
                (!isEnd && data.__video__)
                    ?
                    <Observer onChange={(entry, unobserve) => handleVideoObserverChange(entry, unobserve)} threshold={[0, 0.1, 0.8, 0.9]} rootMargin={`-${headerHeight}px 0px 0px 0px`}>
                        <video
                            src={data.__video__.videoPlayUrl}
                            className="__video__"
                            id={`video_${data.__video__.id}`}
                            muted={mute}
                            poster={formatImage(data.__video__.posterUrl, undefined, 388)}
                            onEnded={() => setIsEnd(true)}
                            onCanPlay={handleCanPlay}
                            playsInline
                            ref={videoRef}
                        />
                    </Observer>
                    : <img src={formatImage(getThumbnail(data), undefined, 388)} alt="" />
            }
            <div className={styles.info}>
                {
                    (!isEnd && data.__video__)
                        ?
                        <>
                            <img src={require(`@src/common/assets/${mute ? 'mute' : 'unMute'}.png`)} alt="" onClick={handleMute} />
                            <div className={styles.nums}>{GetRestDuration()}</div>
                        </>
                        :
                        <>
                            <span className={styles.tag}>{getTags(data)}</span>
                            <div className={styles.nums}>{getNumText(data)}</div>
                        </>
                }
            </div>
        </>
    );
};

FlowVideo.propTypes = {
    isFirst: PropTypes.bool,
    data: PropTypes.object,
    mute: PropTypes.bool,
    onChangeMute: PropTypes.func
};

export default FlowVideo;
