import React from 'react';
import PropTypes from 'prop-types';
import errorBoundary from "@src/components/errorBoundary"
import styles from './index.css';
import { publicPath } from '@src/constants';
import Observer from '@researchgate/react-intersection-observer';
// import { Grounds } from '@ifeng/client_grounds';
import FlowVideo from './FlowVideo';
import IndexCollect from '@src/common/components/IndexCollect';

const Card = ({ title, data, mute, onChangeMute, rnum }) => {

    const toDetail = () => {
        const { base62Id } = data;

        const finalUrl = process.env.NODE_ENV == 'development' ? 
        `${publicPath}/details/${base62Id}?id=phmovie_${base62Id}&type=phmovie&ref=${window.isChannel ? 'fyt' : 'phmovie'}&reftype=${encodeURIComponent('editor|||||||')}&rnum=${rnum}_0&showtype=phbigpic&isFull=0&immersion=true&needpage=1`
        :
        `${window.location.origin}/tvTheater/mobile_details.html`

        if (window.grounds) {
            Grounds.dispatch({
                link: {
                    type: 'web',
                    url: finalUrl
                },
                pageLogParams: {
                    ref: window.isChannel ? 'fyt' : 'phmovie',
                    rnum: `${rnum}_0`,
                    showtype: 'phbigpic',
                    type: 'phmovie',
                    reftype: 'editor|||||||',
                    id: `phmovie_${base62Id}`
                }
            });
        } else {
            window.location.href = finalUrl;
        }
    };

    const handleObserverChange = (entry, unobserve) => {
        const { isIntersecting } = entry;
        const { base62Id } = data;

        if (isIntersecting) {
            // Grounds.expose({
            //     id: `phmovie_${base62Id}`,
            //     rnum: `${rnum}_0`,
            //     reftype: 'editor|||||||',
            //     ch: window.isChannel ? 'fyt' : 'phmovie',
            //     src: '',
            //     showtype: 'phbigpic',
            //     pagetype: 'phmovie'
            // });
            unobserve();
        }
    };

    return (
        <Observer onChange={handleObserverChange}>
            <div className={styles.cardWp}>
                <div className={styles.partTitle}>{title}</div>
                <div className={styles.card} onClick={toDetail}>
                    {
                        !data.__video__ &&
                        <IndexCollect itemData={data} />
                    }
                    <div className={styles.imagePart}>
                        <FlowVideo data={data} mute={mute} onChangeMute={onChangeMute} isFirst={true} />
                    </div>
                    <div className={styles.textPart}>
                        <div className={styles.title}>{data.title}</div>
                        <div className={styles.subTitle}>{data.summary}</div>
                    </div>
                </div>
            </div>
        </Observer>
    );
};

Card.propTypes = {
    title: PropTypes.string,
    data: PropTypes.object,
    mute: PropTypes.bool,
    onChangeMute: PropTypes.func,
    rnum: PropTypes.number
};

export default errorBoundary(Card);
