import React, { useState, useEffect, useCallback } from 'react';
import PropTypes from 'prop-types';
import errorBoundary from "@src/components/errorBoundary"
import Header from './Header';
import Nav from './Nav';
import GuessChase from './GuessChase';
import Hot from './Hot';
import ChannelList from './ChannelList';
import GuessFlow from './GuessFlow';
import SlideList from './SlideList';
// import Chip from 'Chip';
// import ChipEdit from 'ChipEdit';
// import chipDataTransform from 'chipDataTransform';
// import { ChipMap } from '../chipMap';
import styles from './index.css';
import { devPath, publicPath } from '@src/constants';
// import { callApp } from '@ifeng/callapp';
import { setQuery } from '@src/utils/url';
import StickyFill from 'stickyfilljs';
// import { Grounds } from '@ifeng/client_grounds';
// import vConsole from '@src/utils/vConsole';
// vConsole();
let scrollingTimer = null;
let innerHeightTimer = null;
let stickyTop = 0;
let toolbarHeight = 0;

const MODULE_ORDER = ['header', 'nav', 'guessChase', 'channelList', 'hot', 'slideList', 'guessFlow'];

const Layout = props => {
    const { content } = props;

    const [statusBarHeight, setStatusBarHeight] = useState(0); // 状态栏高度
    const [headerOpacity, setHeaderOpacity] = useState(0);
    const [isMute, setIsMute] = useState(true);
    const [isChannel, setIsChannel] = useState(false); // 是否隐藏头部，默认不隐藏，url参数 isChannel=1 时隐藏

    useEffect(() => {
        const params = new URL(window.location.href).searchParams;
        const isChannel = params.get('isChannel') === '1';

        window.isChannel = isChannel;

        setIsChannel(isChannel);
        init(isChannel);
        setPageUnActiveListener(pauseAll);
        if (!isChannel) {
            statistics(); // 频道里不发page统计
            document.addEventListener('scroll', handleScroll);

            return () => {
                document.removeEventListener('scroll', handleScroll);
            };
        }
    }, []);

    const init = async isChannel => {
        if (!isChannel) {
            setIsChannel(false);
            // 沉浸式
            if (window.grounds && Grounds.maximization && Grounds.getStatusBarHeight) {
                const isAndroid = /android/i.test(Grounds.getDeviceInfo().getOs());
                const ratio = isAndroid ? devicePixelRatio : 1;
                const statusBarHeight = Grounds.getStatusBarHeight() / ratio;

                // Grounds.maximization();
                setStatusBarHeight(statusBarHeight);
            }
            // 对于支持sticky的浏览器直接使用CSS吸顶效果，否则使用Stickyfill模拟吸顶
            if (!('CSS' in window && CSS.supports && CSS.supports('position', 'sticky'))) {
                StickyFill.add(document.querySelectorAll('#toolbar'));
            }
        }

        innerHeightTimer = setInterval(() => {
            if (window.innerHeight > 0) {
                clearInterval(innerHeightTimer);
                noTouch();
            }
        }, 100);
    };

    const statistics = () => {
        // const params = new URL(window.location.href).searchParams;
        // const ref = Grounds.getPageInfo?.()?.getRef?.() || params.get('ref') || '';
        // const rnum = Grounds.getPageInfo?.()?.getRnum?.() || params.get('rnum') || '';
        // const showtype = Grounds.getPageInfo?.()?.getShowtype?.() || params.get('showtype') || '';
        // const tag = Grounds.getPageInfo?.()?.getTag?.() || params.get('tag') || '';

        // Grounds.pageStat({
        //     behavior: 'page',
        //     id: 'phmovie',
        //     ref,
        //     type: 'other',
        //     showtype,
        //     rnum,
        //     tag,
        // });
    };

    const setPageUnActiveListener = callback => {
        // 安卓 webview 无法监听 visibilitychange 事件
        const isAndroid = window.grounds ? /android/i.test(Grounds.getDeviceInfo().getOs()) : false;

        if (window.grounds && isAndroid) {
            Grounds.on('onExit', 'historyTask_reGetData');
            window.historyTask_reGetData = () => callback();
        } else {
            document.addEventListener('visibilitychange', () => {
                if (document.visibilityState === 'hidden') {
                    callback();
                }
            });
        }
    };
    const handleScroll = () => {
        const html = document.getElementsByTagName('html')[0];
        const body = document.getElementsByTagName('body')[0];
        const scrollTop = html.scrollTop || body.scrollTop;
        const targetHeight = stickyTop - toolbarHeight - statusBarHeight;

        if (scrollTop >= targetHeight) {
            setHeaderOpacity(1);
        } else {
            setHeaderOpacity(scrollTop / targetHeight);
        }

        if (scrollingTimer) {
            clearTimeout(scrollingTimer);
        }
        scrollingTimer = setTimeout(() => {
            clearTimeout(scrollingTimer);
        }, 300);
    };

    const setStickyTop = node => {
        if (node) {
            stickyTop = node.offsetTop / 1.5;
        }
    };

    const setToolbarHeight = node => {
        if (node) {
            toolbarHeight = node.offsetHeight;
        }
    };

    const getColor = () => {
        const color = parseInt(255 - 255 * headerOpacity);

        return `rgb(${color}, ${color}, ${color})`;
    };

    const back = () => {
        if (window.grounds) {
            Grounds.close();
        } else {
            window.history.back();
        }
    };

    const toSearch = () => {
        // const finalUrl = `${publicPath}/allContent?id=phmoviesrh&ref=phmovie&type=other&isFull=0&needpage=1`;
        const finalUrl = `${devPath}/allContent?id=phmoviesrh&ref=${
            isChannel ? 'fyt' : 'phmovie'
        }&type=other&isFull=0&needpage=1`;

        if (window.grounds) {
            Grounds.dispatch({
                link: {
                    type: 'web',
                    url: finalUrl,
                },
                pageLogParams: {
                    ref: isChannel ? 'fyt' : 'phmovie',
                },
            });
        } else {
            window.location.href = finalUrl;
        }
    };

    const pullClient = () => {
        // callApp({
        //     docType: 'web',
        //     pid: setQuery(window.location.href),
        //     kind: 'inter_da01',
        //     noKindFrom: 1,
        //     download: 'http://a.app.qq.com/o/simple.jsp?pkgname=com.ifeng.news2&ckey=CK1512057577113',
        //     downloadAd: 'https://statistics.appstore.ifeng.com/index.php/api/godownload?app_id=4&d_ch=8927',
        // });
    };

    const pauseAll = () => {
        const videos = document.getElementsByClassName('__video__');

        for (let i = 0; i < videos.length; i++) {
            videos[i].pause();
        }
    };

    const noTouch = () => {
        const isAndroid = window.grounds ? /android/i.test(Grounds.getDeviceInfo().getOs()) : false;

        if (isAndroid) {
            const areas = [];
            const mainSwiper = document.getElementById('mainSwiper').getBoundingClientRect();
            const nav = document.getElementById('nav').getBoundingClientRect();
            const guessChase = document.getElementById('guessChase').getBoundingClientRect();
            const horizontalVideos = document.getElementsByClassName('horizontalVideos');

            areas.push({ top: Math.round(mainSwiper.top), end: Math.round(mainSwiper.bottom), type: 'relative' });
            areas.push({ top: Math.round(nav.top), end: Math.round(nav.bottom), type: 'relative' });
            areas.push({ top: Math.round(guessChase.top), end: Math.round(guessChase.bottom), type: 'relative' });

            for (let i = 0; i < horizontalVideos.length; i++) {
                const rect = horizontalVideos[i].getBoundingClientRect();

                areas.push({ top: Math.round(rect.top), end: Math.round(rect.bottom), type: 'relative' });
            }

            Grounds.setNoSlideArea({
                area: areas,
                totalHeight: window.innerHeight,
            });
        }
    };

    const toggleIsMute = () => setIsMute(!isMute);

    return (
        <div className={styles.layout}>
    
          <div
                id="toolbar"
                ref={useCallback(setToolbarHeight)}
                className={`${styles.toolbar} ${headerOpacity === 0 ? styles.shadow : ''} ${
                    !isChannel ? '' : styles.noHeight
                }`}
                style={{ paddingTop: `${statusBarHeight}px`, background: `rgba(255, 255, 255, ${headerOpacity})` }}>
                {!isChannel && (
                    <div className={styles.iconWp}>
                        <svg
                            width="30px"
                            height="30px"
                            viewBox="0 0 30 30"
                            version="1.1"
                            xmlns="http://www.w3.org/2000/svg"
                            xmlnsXlink="http://www.w3.org/1999/xlink"
                            onClick={back}>
                            <g stroke="none" strokeWidth="1" fill="none" fillRule="evenodd">
                                <g transform="translate(-42.000000, -120.000000)">
                                    <g transform="translate(57.000000, 135.000000) rotate(-270.000000) translate(-57.000000, -135.000000) translate(42.000000, 120.000000)">
                                        <rect id="矩形" x="0" y="0" width="30" height="30" />
                                        <polyline
                                            id="路径-6"
                                            stroke={getColor()}
                                            strokeWidth="1.5"
                                            strokeLinecap="round"
                                            strokeLinejoin="round"
                                            transform="translate(15.000000, 15.000000) scale(1, -1) rotate(90.000000) translate(-15.000000, -15.000000) "
                                            points="19 7 11 15 19 23"
                                        />
                                    </g>
                                </g>
                            </g>
                        </svg>
                        <svg
                            width="74px"
                            height="24px"
                            viewBox="0 0 74 24"
                            version="1.1"
                            xmlns="http://www.w3.org/2000/svg"
                            xmlnsXlink="http://www.w3.org/1999/xlink">
                            <g stroke="none" strokeWidth="1" fill="none" fillRule="evenodd">
                                <g transform="translate(-187.000000, -84.000000)" fill={getColor()}>
                                    <g transform="translate(187.000000, 84.000000)">
                                        <path d="M70.996077,6.03457791 L69.584274,9.3957194 L65.3929954,9.39477058 L63.2423472,22 L53.1847555,22 L54.5965584,18.6388585 L60.4630913,18.6387703 L62.0408315,9.39477058 L53.6597538,9.3957194 L54.2334241,6.03457791 L70.996077,6.03457791 Z M72,0.152580299 L70.5881971,3.51372179 L53.825073,3.51361579 L51.2440278,18.6388585 C51.0528044,19.759239 50.5822034,20.8796195 49.8322249,22 L46.1954983,22 C47.1844855,20.5893367 47.9409203,18.349288 48.4648028,15.2798541 L51.0466839,0.152580299 L72,0.152580299 Z M43.8906903,0.149155538 L43.6033893,1.83219331 L48.2454508,1.83315105 L46.5244399,11.9165755 L47.3625725,11.9165755 L46.7889022,15.277717 L43.4032229,15.2770179 L46.060628,22 L41.6604316,22 L39.484034,15.835307 L35.204191,22 L30.8039947,22 L35.7554197,15.2770179 L31.7025146,15.277717 L30.555174,22 L22.1738476,22 L25.9027044,0.152580836 L34.2840309,0.152580836 L32.2761849,11.9165755 L33.1143176,11.9165755 L34.8353285,1.83315105 L39.4120974,1.83219331 L39.7000271,0.149155538 L43.8906903,0.149155538 Z M11.5562467,0 L11.2694116,1.68057075 L14.4198545,1.68057075 L13.2101392,5.04171224 L7.342784,5.04098982 L7.05637557,6.72228298 L12.9233041,6.72228298 L10.9154581,18.4862782 C10.7242346,19.6066587 10.2536336,20.7270392 9.50365511,21.8474197 L5.86692852,21.8474197 C6.85591572,20.4367564 7.61235055,18.1967077 8.13623302,15.1272738 L8.9963383,10.0830722 L6.48270528,10.0834245 L5.04852953,18.4862782 C4.8573061,19.6066587 4.38670512,20.7270392 3.63672659,21.8474197 L0,21.8474197 C0.988987201,20.4367564 1.74542204,18.1967077 2.2693045,15.1272738 L3.9906201,5.04098982 L2.31441485,5.04171224 L3.72621779,1.68057075 L7.07874837,1.68057075 L7.36558352,0 L11.5562467,0 Z M19.0994406,0 L18.5326447,1.68032994 L24.6795339,1.68057075 L22.958523,11.7639952 L19.0585544,16.4307425 L21.6761476,21.8474197 L17.8849816,21.8474197 L16.6383222,19.3259794 L14.532451,21.8474197 L10.3417878,21.8474197 L15.1339264,16.2821568 L12.9009313,11.7639952 L13.7614367,6.72228298 L17.1139673,6.72228298 L16.3968794,10.9237099 L17.6012976,13.4164185 L19.74941,10.9237099 L20.7525265,5.04098982 L14.0482719,5.04171224 L15.74691,0 L19.0994406,0 Z M28.9666701,11.6639808 L27.2895012,11.6639808 L26.1000485,18.6388585 L27.7763137,18.6388585 L28.9666701,11.6639808 Z M44.3187413,5.19394573 L37.6133265,5.19394573 L36.4668481,11.9165755 L43.1719093,11.9165755 L44.3187413,5.19394573 Z M39.6208708,6.04105206 L42.9988709,8.72259069 L38.3468226,10.9721231 L39.6208708,6.04105206 Z M30.3578301,3.51372179 L28.6815648,3.51372179 L27.8632758,8.30222835 L29.5404447,8.30222835 L30.3578301,3.51372179 Z" />
                                    </g>
                                </g>
                            </g>
                        </svg>
                        <svg
                            width="30px"
                            height="30px"
                            viewBox="0 0 30 30"
                            version="1.1"
                            xmlns="http://www.w3.org/2000/svg"
                            xmlnsXlink="http://www.w3.org/1999/xlink"
                            onClick={toSearch}>
                            <g
                                stroke="none"
                                strokeWidth="1"
                                fill="none"
                                fillRule="evenodd"
                                strokeLinecap="round"
                                strokeLinejoin="round">
                                <g transform="translate(-83.000000, -120.000000)" stroke={getColor()} strokeWidth="1.5">
                                    <g transform="translate(83.000000, 120.000000)">
                                        <g transform="translate(5.907047, 5.962636)">
                                            <g transform="translate(10.286745, 10.286745) rotate(-45.000000) translate(-10.286745, -10.286745) translate(4.286745, 1.739091)">
                                                <circle id="椭圆形" cx="6" cy="6" r="6.75" />
                                                <line
                                                    x1="6.03930747"
                                                    y1="13.1840312"
                                                    x2="6"
                                                    y2="17.0953085"
                                                    id="Stroke-3"
                                                />
                                            </g>
                                        </g>
                                    </g>
                                </g>
                            </g>
                        </svg>
                    </div>
                )}
            </div> 

            {MODULE_ORDER.map((module, index) => {
                switch (module) {
                    case 'header': {
                        return (
                            <div key={index}>
                                {/* <Chip key="Swiper" {...ChipMap.Swiper} content={content.Swiper.slice(0, 10)}>
                                    <Header statusBarHeight={statusBarHeight} rnum={index} />
                                </Chip> */}
                                <Header statusBarHeight={statusBarHeight} rnum={index} content={content.Swiper.slice(0, 10)}/>
                                <div ref={useCallback(setStickyTop)} />
                            </div>
                        );
                    }
                    case 'nav': {
                        return (
                            <>
                                {/* <Chip key="Nav" {...ChipMap.Nav} content={content.Nav}>
                                <Nav rnum={index} />
                                </Chip> */}
                                <Nav rnum={index} content={content.Nav}/>
                            </>

                        );
                    }
                    case 'guessChase': {
                        return <GuessChase key={index} rnum={index} />;
                    }
                    case 'channelList': {
                        return (
                            <>
                                {/* <Chip key="channelList" {...ChipMap.channelList} content={content.channelList}>
                                <ChannelList key={index} rnum={index} />
                                </Chip> */}
                                <ChannelList key={index} rnum={index} content={content.ChannelList}/>
                            </>
                    
                        );
                    }
                    case 'hot': {
                        return (
                            <>
                                           {/* <Chip key="Hot" {...ChipMap.Hot} content={content.Hot.slice(0, 24)}>
                                <Hot key={index} rnum={index} />
                                </Chip> */}
                                <Hot key={index} rnum={index} content={content.Hot.slice(0, 24)}/>
                            </>
                 
                        );
                    }
                    case 'slideList': {
                        return (
                            <>
                                      {/* <Chip key="SlideList" {...ChipMap.SlideList} content={content.SlideList}>
                                <SlideList mute={isMute} onChangeMute={toggleIsMute} rnum={index} />
                                </Chip> */}
                                <SlideList mute={isMute} onChangeMute={toggleIsMute} rnum={index} content={content.SlideList}/>
                            </>
                      
                        );
                    }
                    case 'guessFlow': {
                        return (
                            <GuessFlow
                                key={index}
                                mute={isMute}
                                onChangeMute={toggleIsMute}
                                rnum={index + content.SlideList.length + 1}
                            />
                        );
                    }
                    default:
                        return null;
                }
            })}
{/* 
            {!window.grounds && (
                <div className={styles.test} onClick={pullClient}>
                    端内测试
                </div>
            )} */}
            {/* <ChipEdit transform={chipDataTransform} />  */}
        </div>
    );
};

Layout.propTypes = {
    content: PropTypes.object,
};

export default errorBoundary(Layout);
