import React, { useState, useEffect, useRef } from 'react';
import PropTypes from 'prop-types';
import styles from './index.css';
import errorBoundary from "@src/components/errorBoundary"
import { guessyoulike } from '@src/mobile/index/services';
// import LoadingMore from '@src/components/LoadingMore';
// import { pageStatistics } from '@ifeng/mobile_statistics';
import { publicPath, devPath } from '@src/constants';
import { formatImage } from '@ifeng/public_method';
import Observer from '@researchgate/react-intersection-observer';
// import { Grounds } from '@ifeng/client_grounds';
import dayjs from 'dayjs';
import duration from 'dayjs/plugin/duration';
import FlowVideo from '@src/mobile/index/layout/GuessFlow/FlowVideo';
import IndexCollect from '@src/common/components/IndexCollect';

dayjs.extend(duration);

const GuessFlow = props => {
    const { mute, onChangeMute, rnum } = props;
    const [videoList, setVideoList] = useState([]);
    const [dataNum, setDataNum] = useState(0);

    const statisticsList = useRef([]);

    useEffect(() => {
        fetchData();
    }, []);

    const fetchData = async () => {
        const res = await guessyoulike({ pageSize: 7 });

        if (res.code === 0) {
            setDataNum(res.data.items.length);
            setVideoList(res.data.items);
        }
    };

    const control = async () => {
        let statusText = 'error';

        const res = await guessyoulike({ pageSize: dataNum === 0 ? 7 : 14 - dataNum });

        const newList = res.data.items;

        if (newList && Array.isArray(newList)) {
            const length = res.data.items.length;

            pageStatistics({
                behavior: 'action',
                environment: 'client',
                type: 'pnp',
                id: window.isChannel ? 'fyt' : 'phmovie',
                rfnum: length,
            });
            statusText = 'success';
            setDataNum(length);
            setVideoList([...videoList, ...newList]);
        }

        return statusText;
    };

    const getSplitVideoList = () => {
        const arr = [];
        let index = 0;

        while (index <= videoList.length) {
            arr.push(videoList.slice(index, index + 7 < videoList.length ? index + 7 : videoList.length));
            index += 7;
        }

        const last = [...arr[arr.length - 1]];

        // 1 + n，n 为单数情况，隐藏掉最后一个
        if (last.length % 2 === 0) {
            last.pop();
        }
        arr[arr.length - 1] = last;

        return arr;
    };

    const jumpToDetail = (item, index, type) => {
        const showtype = type === 'large' ? 'glbigpix' : 'glsmallpix';
        const { id, reftype } = item;

        const finalUrl = process.env.NODE_ENV == 'development' ? 
        `${devPath}/details/${id.replace('ucms_', '')}?id=phmovie_${id.replace(
            'ucms_',
            '',
        )}&type=phmovie&ref=${window.isChannel ? 'fyt' : 'phmovie'}&reftype=${encodeURIComponent(
            reftype,
        )}&rnum=${rnum}_${index}&showtype=${showtype}&isFull=0&immersion=true&needpage=1`
        :
        `${window.location.origin}/tvTheater/mobile_details.html`


        if (window.grounds) {
            Grounds.dispatch({
                link: {
                    type: 'web',
                    url: finalUrl,
                },
                pageLogParams: {
                    id: `phmovie_${id.replace('ucms_', '')}`,
                    ref: window.isChannel ? 'fyt' : 'phmovie',
                    rnum: `${rnum}_${index}`,
                    showtype,
                    type: 'phmovie',
                    reftype,
                },
            });
        } else {
            window.location.href = finalUrl;
        }
    };

    const handleObserverChange = (entry, unobserve, item, index, type) => {
        const { isIntersecting } = entry;

        if (isIntersecting) {
            const { reftype, staticId } = item;

            if (!statisticsList.current.includes(staticId)) {
                // Grounds.expose({
                //     id: staticId,
                //     rnum: `${rnum}_${index}`,
                //     reftype,
                //     ch: window.isChannel ? 'fyt' : 'phmovie',
                //     src: '',
                //     showtype: type === 'large' ? 'glbigpix' : 'glsmallpix',
                //     pagetype: 'phmovie',
                // });
                const newList = [...statisticsList.current];

                newList.push(staticId);
                statisticsList.current = newList;
            }
            unobserve();
        }
    };

    return (
        <div className={styles.guessFlow}>
            <div className={styles.partTitle}>精彩推荐</div>
            {getSplitVideoList().map((video, index) => {
                const first = video[0] || {};
                const last = video.slice(1);

                return (
                    <div key={index}>
                        {first.id && (
                            <Observer
                                onChange={(entry, unobserve) =>
                                    handleObserverChange(entry, unobserve, first, index * 7, 'large')
                                }>
                                <div className={styles.card} onClick={() => jumpToDetail(first, index * 7, 'large')}>
                                    {!first.mp4 && (
                                        <IndexCollect
                                            itemData={{
                                                base62Id: first.id.replace('ucms_', ''),
                                                posterUrl: first.thumbnail,
                                                title: first.title,
                                            }}
                                        />
                                    )}
                                    <div className={styles.imagePart}>
                                        <FlowVideo data={first} mute={mute} onChangeMute={onChangeMute} />
                                    </div>
                                    <div className={styles.textPart}>
                                        <div className={styles.title}>{first.title}</div>
                                        <div className={styles.subTitle}>{first.subhead}</div>
                                    </div>
                                </div>
                            </Observer>
                        )}
                        <div className={styles.videoList}>
                            {last.map((item, idx) => {
                                const rnum = index * 7 + idx + 1;

                                return (
                                    <Observer
                                        onChange={(entry, unobserve) =>
                                            handleObserverChange(entry, unobserve, item, rnum, 'small')
                                        }
                                        key={`${index}-${idx}`}>
                                        <div
                                            className={styles.videoItem}
                                            onClick={() => jumpToDetail(item, rnum, 'small')}>
                                            <IndexCollect
                                                itemData={{
                                                    base62Id: item.id.replace('ucms_', ''),
                                                    posterUrl: item.thumbnail,
                                                    title: item.title,
                                                }}
                                            />
                                            <div className={styles.imagePart}>
                                                <img src={formatImage(item.thumbnail, 344, 194)} alt={item.title} />
                                                <div className={styles.info}>
                                                    <span className={styles.tag}>{item.tags}</span>
                                                    <div className={styles.nums}>{item.numText}</div>
                                                </div>
                                            </div>
                                            <div className={styles.title}>{item.title}</div>
                                            <div className={styles.subTitle}>{item.subhead}</div>
                                        </div>
                                    </Observer>
                                );
                            })}
                        </div>
                    </div>
                );
            })}
            {/* <LoadingMore control={control} /> */}
        </div>
    );
};

GuessFlow.propTypes = {
    mute: PropTypes.bool,
    onChangeMute: PropTypes.func,
    rnum: PropTypes.number
};

export default errorBoundary(GuessFlow);
