import React from 'react';
import PropTypes from 'prop-types';
import errorBoundary from "@src/components/errorBoundary"
import HorizontalVideos from './HorizontalVideos';
import Card from './Card';

const SlideList = ({ content, mute, onChangeMute, rnum }) => {
    const [ first, ...other ] = content;

    return (
        <div>
            <Card title={first.title} data={first.list[0]} mute={mute} onChangeMute={onChangeMute} rnum={rnum} />
            {
                other.slice(0, 6).map((item, index) => {
                    return (
                        <HorizontalVideos key={index} title={item.title} list={item.list} rnum={rnum + index + 1} />
                    );
                })
            }
        </div>
    );
};

SlideList.propTypes = {
    content: PropTypes.array,
    mute: PropTypes.bool,
    onChangeMute: PropTypes.func,
    rnum: PropTypes.number
};

export default errorBoundary(SlideList);
