import React from 'react';
import PropTypes from 'prop-types';
import errorBoundary from "@src/components/errorBoundary"
import styles from './index.css';
import { formatImage } from '@ifeng/public_method';
import { publicPath } from '@src/constants';
import Observer from '@researchgate/react-intersection-observer';
// import { Grounds } from '@ifeng/client_grounds';
import IndexCollect from '@src/common/components/IndexCollect';

const HorizontalVideos = ({ title, list, rnum }) => {

    const getThumbnail = (data) => {
        const image = data.miniVideoPoster || (data.thumbnails && data.thumbnails.image && data.thumbnails.image[0] && data.thumbnails.image[0].url);

        return image || '';
    };

    const getTags = (data) => {
        if (data.extData && data.extData.tags) {
            const arr = data.extData.tags.split(',');

            return arr[0];
        } else {
            return '';
        }
    };

    const getNumText = (data) => {
        if (data.extData && data.extData.dataTotal) {
            return `全${data.extData.dataTotal}集`;
        } else {
            return '';
        }
    };

    const toDetail = (data, index) => {
        const { base62Id } = data;

        const finalUrl = process.env.NODE_ENV == 'development' ? 
        `${publicPath}/details/${base62Id}?id=phmovie_${base62Id}&type=phmovie&ref=${window.isChannel ? 'fyt' : 'phmovie'}&reftype=${encodeURIComponent('editor|||||||')}&rnum=${rnum}_${index}&showtype=nineshow&isFull=0&immersion=true&needpage=1`
        :
        `${window.location.origin}/tvTheater/mobile_details.html`



        if (window.grounds) {
            Grounds.dispatch({
                link: {
                    type: 'web',
                    url: finalUrl
                },
                pageLogParams: {
                    ref: window.isChannel ? 'fyt' : 'phmovie',
                    rnum: `${rnum}_${index}`,
                    showtype: 'nineshow',
                    type: 'phmovie',
                    reftype: 'editor|||||||',
                    id: `phmovie_${base62Id}`
                }
            });
        } else {
            window.location.href = finalUrl;
        }
    };

    const handleObserverChange = (entry, unobserve, item, index) => {
        const { isIntersecting } = entry;
        const { base62Id } = item;

        if (isIntersecting) {
            // Grounds.expose({
            //     id: `phmovie_${base62Id}`,
            //     rnum: `${rnum}_${index}`,
            //     reftype: 'editor|||||||',
            //     ch: window.isChannel ? 'fyt' : 'phmovie',
            //     src: '',
            //     showtype: 'nineshow',
            //     pagetype: 'phmovie'
            // });
            unobserve();
        }
    };

    return (
        <div className={`${styles.horizontalVideos} horizontalVideos`}>
            <div className={styles.partTitle}>{title}</div>
            <div className={styles.videoList}>
                {
                    list.map((video, index) => {
                        return (
                            <Observer key={index} onChange={(entry, unobserve) => handleObserverChange(entry, unobserve, video, index)}>
                                <div className={styles.videoItem} onClick={() => toDetail(video, index)}>
                                    <IndexCollect itemData={video} />
                                    <div className={styles.imagePart}>
                                        <img src={formatImage(getThumbnail(video), 226, 304)} alt={video.title} />
                                        <div className={styles.info}>
                                            <span className={styles.tag}>{getTags(video)}</span>
                                            <div className={styles.nums}>{getNumText(video)}</div>
                                        </div>
                                    </div>
                                    <div className={styles.title}>{video.title}</div>
                                    <div className={styles.subTitle}>{video.summary}</div>
                                </div>
                            </Observer>
                        );
                    })
                }
            </div>
        </div>
    );
};

HorizontalVideos.propTypes = {
    title: PropTypes.string,
    list: PropTypes.array,
    rnum: PropTypes.number
};

export default errorBoundary(HorizontalVideos);
