import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import styles from './index.css';
import errorBoundary from "@src/components/errorBoundary"
import { guessyouchase } from '@src/mobile/index/services';
import { publicPath, devPath } from '@src/constants';
import { formatImage } from '@ifeng/public_method';
import Observer from '@researchgate/react-intersection-observer';
import { i2s } from '@src/utils/format';
// import { Grounds } from '@ifeng/client_grounds';
import nzhcn from 'nzh/cn';

const GuessChase = props => {
    const { rnum } = props;

    const [videoList, setVideoList] = useState([]);
    const [firstHistory, setFirstHistory] = useState(null);
    const [isShowHistory, setIsShowHistory] = useState(false);

    useEffect(() => {
        fetchData();
        getHistory();
        setPageActiveListener(getHistory);
    }, []);

    const fetchData = async () => {
        const res = await guessyouchase();

        if (res.code === 0) {
            setVideoList(res.data.items);
            setIsShowHistory(res.data.vsInsert);
        }
    };

    const getHistory = () => {
        let list = JSON.parse(localStorage.getItem('history'));

        if (list) {
            list.sort((a, b) => (b.createTime > a.createTime ? 1 : -1));
            list = list.map(a => {
                return {
                    ...a,
                    videoSeries: a.videoSeries
                        .filter(v => v.type !== 'gags')
                        .sort((a, b) => (new Date(b.createTime) > new Date(a.createTime) ? 1 : -1)),
                };
            });

            setFirstHistory(
                list.filter(a => {
                    if (!a.videoSeries || (a.videoSeries && a.videoSeries.length < 1)) {
                        return false;
                    }

                    let totalSeconds = 0;
                    let currentSeconds = 0;

                    const totalArr = a.videoSeries[0].duration.split(':');
                    const currentArr = i2s(a.videoSeries[0].currentTime).split(':');

                    if (totalArr.length === 2) {
                        const [totalMinute, totalSecond] = totalArr;

                        totalSeconds = Number(totalMinute) * 60 + Number(totalSecond);
                    } else if (totalArr.length === 3) {
                        const [totalHour, totalMinute, totalSecond] = totalArr;

                        totalSeconds = Number(totalHour) * 60 * 60 + Number(totalMinute) * 60 + Number(totalSecond);
                    }

                    if (currentArr.length === 2) {
                        const [currentMinute, currentSecond] = currentArr;

                        currentSeconds = Number(currentMinute) * 60 + Number(currentSecond);
                    } else if (currentArr.length === 3) {
                        const [currentHour, currentMinute, currentSecond] = currentArr;

                        currentSeconds =
                            Number(currentHour) * 60 * 60 + Number(currentMinute) * 60 + Number(currentSecond);
                    }

                    const percent = currentSeconds / totalSeconds;

                    return percent < 0.9;
                })[0],
            );
        }
    };

    const setPageActiveListener = callback => {
        // 安卓 webview 无法监听 visibilitychange 事件
        const isAndroid = window.grounds ? /android/i.test(Grounds.getDeviceInfo().getOs()) : false;

        if (window.grounds && isAndroid) {
            Grounds.on('active', 'historyTask_reGetData');
            window.historyTask_reGetData = () => callback();
        } else {
            document.addEventListener('visibilitychange', () => {
                if (document.visibilityState === 'visible') {
                    callback();
                }
            });
        }
    };

    const handleObserverChange = (entry, unobserve, type, item, index) => {
        const { isIntersecting } = entry;
        const isHistory = type === 'history';

        if (isIntersecting) {
            let id = '';
            let reftype = '';

            if (isHistory) {
                id = `phmovie_${firstHistory.id}`;
                reftype = 'user|||||||';
            } else {
                id = item.staticId;
                reftype = item.reftype;
            }

            // Grounds.expose({
            //     id,
            //     rnum: `${rnum}_${index}`,
            //     reftype,
            //     ch: window.isChannel ? 'fyt' : 'phmovie',
            //     src: '',
            //     showtype: 'guesschase',
            //     pagetype: 'phmovie',
            // });
            unobserve();
        }
    };

    const toDetail = (type, item, index) => {
        const isHistory = type === 'history';
        let id = '';
        let reftype = '';

        if (isHistory) {
            id = firstHistory.id;
            reftype = encodeURIComponent('user|||||||');
        } else {
            id = item.id.replace('ucms_', '');
            reftype = encodeURIComponent(item.reftype);
        }


        const finalUrl = process.env.NODE_ENV == 'development' ? 
        `${devPath}/details/${id}?id=phmovie_${id}&type=phmovie&ref=${
            window.isChannel ? 'fyt' : 'phmovie'
        }&reftype=${reftype}&rnum=${rnum}_${index}&showtype=guesschase${
            isHistory ? `&positiveId=${firstHistory.videoSeries[0].id}&source=positive` : ''
        }&isFull=0&immersion=true&needpage=1`
        :
        `${window.location.origin}/tvTheater/mobile_details.html`

        if (window.grounds) {
            Grounds.dispatch({
                link: {
                    type: 'web',
                    url: finalUrl,
                },
                pageLogParams: {
                    id: `phmovie_${id}`,
                    ref: window.isChannel ? 'fyt' : 'phmovie',
                    rnum: `${rnum}_${index}`,
                    showtype: 'guesschase',
                    type: 'phmovie',
                    reftype,
                },
            });
        } else {
            window.location.href = finalUrl;
        }
    };

    return (
        <div className={styles.guessChase} id="guessChase">
            <div className={styles.partTitle}>猜你想追</div>
            <div className={styles.videoList}>
                {isShowHistory &&
                    firstHistory && (
                        <Observer
                            key="firstHistory"
                            onChange={(entry, unobserve) =>
                                handleObserverChange(entry, unobserve, 'history', undefined, 0)
                            }>
                            <div
                                data-index={0}
                                className={styles.videoItem}
                                onClick={() => toDetail('history', undefined, 0)}>
                                <div className={styles.imagePart}>
                                    <img src={formatImage(firstHistory.posterUrl, 313, 176)} alt="" />
                                    <div className={styles.nums}>全{firstHistory.total}集</div>
                                </div>
                                <div className={styles.title}>{firstHistory.title}</div>
                                <div className={styles.subTitle}>
                                    上次看到{' '}
                                    {firstHistory.extText ||
                                        `第${nzhcn.encodeS(Number(firstHistory.currentIndex) + 1)}集`}
                                </div>
                            </div>
                        </Observer>
                    )}
                {videoList.filter(v => v.id !== (firstHistory ? `${firstHistory.id}` : '')).map((video, index) => {
                    return (
                        <Observer
                            key={`chase_${index}`}
                            onChange={(entry, unobserve) =>
                                handleObserverChange(entry, unobserve, '', video, firstHistory ? index + 1 : index)
                            }>
                            <div
                                className={styles.videoItem}
                                onClick={() => toDetail('', video, firstHistory ? index + 1 : index)}>
                                <div className={styles.imagePart}>
                                    <img src={formatImage(video.thumbnail, 313, 176)} alt={video.title} />
                                    <div className={styles.nums}>{video.numText}</div>
                                </div>
                                <div className={styles.title}>{video.title}</div>
                                <div className={styles.subTitle}>{video.subhead}</div>
                            </div>
                        </Observer>
                    );
                })}
            </div>
        </div>
    );
};

GuessChase.propTypes = {
    rnum: PropTypes.number,
};

export default errorBoundary(GuessChase);
