import React, { useEffect, useState } from 'react';
import styles from './index.css';
import PropTypes from 'prop-types';
import errorBoundary from "@src/components/errorBoundary"
import { publicPath, devPath } from '@src/constants';
// import { Grounds } from '@ifeng/client_grounds';

const Nav = props => {
    const { content, rnum } = props;

    const [avatar, setAvaTar] = useState('');

    useEffect(() => {
        if (window.grounds) {
            const avatar = window.grounds.getUserInfo().getUserImage();

            if (avatar) {
                setAvaTar(avatar);
            }
        }
    }, []);

    const jumpTo = (item, index) => {
        const { url, type, params } = item;
        let path = ''; // 碎片详情
        let tongjiType = '';
        let tongjiId = undefined;

        if (url) {
            // 历史
            path = url;
            tongjiType = 'phhistory';
            tongjiId = 'history';
        } else {
            // 分类
            path =
                type === 'cate'
                    ? `allContent?label=${encodeURIComponent(params)}`
                    : `details/${params}?id=phmovie_${params}&type=phmovie`;
            tongjiType = type === 'cate' ? 'phall' : 'phmovie';
            tongjiId = type === 'cate' ? 'phall' : `phmovie_${params}`;
        }

        const finalUrl = process.env.NODE_ENV == 'development' ? 
        `${devPath}/${path}${
            path.includes('?') ? '&' : '?'
        }id=${tongjiId}&ref=${window.isChannel ? 'fyt' : 'phmoive'}&reftype=${encodeURIComponent(
            'editor|||||||',
        )}&type=${tongjiType}&rnum=${rnum}_${index}&showtype=homemenu&isFull=0&needpage=1&ifengsp=1${
            type !== 'cate' ? '&immersion=true' : ''
        }`
        :
        `${window.location.origin}/tvTheater/mobile_${url? "history":"allContent"}.html`


        if (window.grounds) {
            // Grounds.dispatch({
            //     link: {
            //         type: 'web',
            //         url: finalUrl,
            //     },
            //     pageLogParams: {
            //         ref: window.isChannel ? 'fyt' : 'phmoive',
            //         rnum: `${rnum}_${index}`,
            //         showtype: 'homemenu',
            //         type: tongjiType,
            //         reftype: 'editor|||||||',
            //         id: tongjiId,
            //     },
            // });
        } else {
            window.location.href = finalUrl;
        }
    };

    return (
        <div className={styles.nav} id="nav">
            <div className={styles.navItem} onClick={() => jumpTo({ url: 'history' }, 0)}>
                <img
                    src={
                        avatar ||
                        'https://x0.ifengimg.com/ucms/2022_12/4349AD3D58C50EBCC7CE62CB53262273EC6F5C69_size10_w160_h160.png'
                    }
                    alt="history"
                />
                <span>观看记录</span>
            </div>
            {content.map((c, index) => {
                return (
                    <div className={styles.navItem} key={index} onClick={() => jumpTo(c, index + 1)}>
                        <img src={c.icon} alt={c.label} />
                        <span>{c.label}</span>
                    </div>
                );
            })}
        </div>
    );
};

Nav.propTypes = {
    content: PropTypes.array,
    rnum: PropTypes.number
};

export default errorBoundary(Nav);
