import React, { useEffect } from 'react';
import styles from './index.css';
import PropTypes from 'prop-types';
import errorBoundary from "@src/components/errorBoundary"
import Swiper, { Autoplay, Pagination } from 'swiper';
import 'swiper/css';
import { formatImage } from '@ifeng/public_method';
import { devPath, publicPath } from '@src/constants';
import Observer from '@researchgate/react-intersection-observer';
// import { Grounds } from '@ifeng/client_grounds';

Swiper.use([Autoplay, Pagination]);
console.log('process.env.NODE_ENV',process.env.NODE_ENV);
const MainSwiper = props => {
    const { content, onChangeIndex, rnum } = props;

    useEffect(() => {
        new Swiper('#swiper1', {
            loop: true,
            autoplay: {
                delay: 5000,
                disableOnInteraction: false,
            },
            spaceBetween: 20,
            pagination: {
                el: '.pagination1',
            },
            on: {
                slideChangeTransitionStart() {
                    onChangeIndex(this.realIndex);
                },
            },
        });
    }, []);

    const toDetail = e => {
        let { index } = e.currentTarget.dataset;

        index = +index;
        const { base62Id } = content[index];

        const finalUrl = process.env.NODE_ENV == 'development' ? 
        `${devPath}/details/${base62Id}?id=phmovie_${base62Id}&type=phmovie&ref=${
            window.isChannel ? 'fyt' : 'phmovie'
        }&reftype=${encodeURIComponent(
            'editor|||||||',
            )}&rnum=${rnum}_${index}&showtype=toprotation&isFull=0&immersion=true&needpage=1`
        :
        `${window.location.origin}/tvTheater/mobile_details.html`
        
  

        if (window.grounds) {
            // Grounds.dispatch({
            //     link: {
            //         type: 'web',
            //         url: finalUrl,
            //     },
            //     pageLogParams: {
            //         ref: window.isChannel ? 'fyt' : 'phmovie',
            //         rnum: `${rnum}_${index}`,
            //         showtype: 'toprotation',
            //         type: 'phmovie',
            //         reftype: 'editor|||||||',
            //         id: `phmovie_${base62Id}`,
            //     },
            // });
        } else {
            window.location.href = finalUrl;
        }
    };

    const handleObserverChange = (entry, unobserve, item, index) => {
        const { isIntersecting } = entry;

        if (isIntersecting) {
            // Grounds.expose({
            //     id: `phmovie_${item.base62Id}`,
            //     rnum: `${rnum}_${index}`,
            //     reftype: 'editor|||||||',
            //     ch: window.isChannel ? 'fyt' : 'phmovie',
            //     src: '',
            //     showtype: 'toprotation',
            //     pagetype: 'phmovie',
            // });
            unobserve();
        }
    };

    return (
        <div className={styles.mainSwiper} id="mainSwiper">
            <div className={`${styles.swiper} swiper`} id="swiper1">
                <div className="swiper-wrapper">
                    {content.map((a, index) => {
                        return (
                            <div className="swiper-slide" key={index}>
                                <Observer
                                    onChange={(entry, unobserve) => handleObserverChange(entry, unobserve, a, index)}>
                                    <div className={styles.swiperItem} data-index={index} onClick={toDetail}>
                                        <img src={formatImage(a.posterUrl, 690, 388)} alt={a.title} />
                                        <div className={styles.textPart}>
                                            {a.summary || ''}
                                        </div>
                                    </div>
                                </Observer>
                            </div>
                        );
                    })}
                </div>
            </div>
            <div className={`${styles.pagination} pagination1`} />
        </div>
    );
};

MainSwiper.propTypes = {
    content: PropTypes.array,
    onChangeIndex: PropTypes.func,
    rnum: PropTypes.number
};

export default errorBoundary(MainSwiper);
