import React from 'react';
import styles from './index.css';
import PropTypes from 'prop-types';
import errorBoundary from "@src/components/errorBoundary"
import { formatImage } from '@ifeng/public_method';
// import { Grounds } from '@ifeng/client_grounds';

const ChannelList = (props) => {
	const { content, rnum } = props;

	console.log('ChannelList',content);

	const jumpToDetail = (item, index) => {
		if (window.grounds) {
			Grounds.dispatch({
				type: 'phtvsolenewslist',
				url: item.wemediaEAccountId
			});
		}
	};

	const toMore = () => {
		if (window.grounds) {
			Grounds.dispatch({
				link: {
					type: 'web',
					url: 'https://news.ifeng.com/loc/satelliteTV/solenews/tvTheater?isFull=0&immersion=true'
				},
				pageLogParams: {
					ref: window.isChannel ? 'fyt' : 'phmovie',
					rnum: `${rnum}`,
				},
			});
		}
	};

	return (
		<div className={styles.hot}>
			<div className={styles.toolbar}>
				<div className={styles.partTitle}>精选栏目</div>
			</div>
			<div className={styles.videoList}>
				{
					content.slice(0, 6).map((a, index) => {
						return (
							<div key={index} className={styles.videoItem} onClick={() => jumpToDetail(a, index)}>
								<div className={styles.imagePart}>
									<img src={formatImage(a.headlineVerticalImage || a.smallPoster, 226, 304)} alt={a.title} />
								</div>
								<div className={styles.title}>{a.title}</div>
								<div className={styles.subTitle}>{a.phtvDesc}</div>
							</div>
						);
					})
				}
			</div>
			<div className={styles.more} onClick={toMore}>
				查看全部卫视栏目
			</div>
		</div>
	);
};

ChannelList.propTypes = {
	content: PropTypes.array,
	rnum: PropTypes.number
};

export default errorBoundary(ChannelList);

