import Axios from 'axios';
import { jsonp } from '@ifeng/ui_base';
// import { Grounds } from '@ifeng/client_grounds';
import { getClientParams } from '@src/utils/app';

// axios实例
export const axios = Axios.create();

// axios实例的基础路径
// eslint-disable-next-line no-undef
axios.defaults.baseURL = apiUrl;

// 客户端标参
const clientParams = getClientParams();

axios.interceptors.request.use(
    config => {
        return { ...config, params: { ...config.params, ...clientParams } };
    },
    err => {
        return Promise.reject(err);
    },
);

axios.interceptors.response.use(
    response => {
        const { status, data } = response;

        return { ...data, httpStatus: status };
    },
    err => {
        return Promise.resolve(err);
    },
);

const request = async ({ url, params, name }) => {
    if (window.grounds) {
        return await Grounds.nativeRequest({
                // eslint-disable-next-line no-undef
                url: apiUrl + url,
                getParams: JSON.stringify(params),
                type: 'get',
            });
    } else {
        // eslint-disable-next-line no-undef
        return await jsonp(apiUrl + url, {
            data: params,
            jsonpCallback: name + Date.now(),
            cache: false,
        });
    }
};

// 猜你想追
export const guessyouchase = async () => {
    return await request({
        url: '/recommend',
        params: {
          from: 'screen_chase'
        },
        name: 'guessyouchase'
    });
};

// 猜你喜欢
export const guessyoulike = async (params) => {
    // mock
    return await request({
        url: '/recommend',
        params: {
            ...params,
            from: 'screen_favor'
        },
        name: 'guessyoulike'
    });
};
